<template lang="pug">
scrollactive#projectRightNav(:class="{'show': scrollPosition > 100}" :modifyUrl="false" :alwaysTrack="true")
  a.scrollactive-item(v-for="navLink in rightNavContent" :href="'#'+ navLink") {{navLink}}
</template>

<script>
export default {
  props: ["rightNavContent"],
  data() {
    return {
      scrollPosition: null
    }
  },
  methods: {
    updateScroll() {
      this.scrollPosition = window.scrollY
    }
  },
  mounted() {
    window.addEventListener('scroll', this.updateScroll);
  }    
}
</script>

