<template lang="pug">
#treepoint
  projectNav(:class="{'fixed': scrollPosition > 70}",projectName="TreePoint")
  projectRightNav(:rightNavContent="rightNavItem")
  .heroSection(:class="{'marginTop': scrollPosition > 70}")
    projectCard(v-bind:projectCard="projectIntro")
  .viewContent
    section#background
      sectionTitle(secTitle="Background",secDes="A new point for united reward program") 
      .paraSec
        .para
          p
            |Cathay Financial Group is the top 5 Financial enterprise in Taiwan. 
            |There are many companies in Cathay Financial Group like Cathay United Bank, Cathay Life Insurance, Cathay Century Insurance and Cathay Securities Corporation and so on.
          p
            |There are a rewarding point in Cathay Group named “RedPoint” published by Cathay United Bank.
            |TreePoint is a brand new reward point for the whole group which is published by non-financial subsidiary and it’s free from some retrict of financial laws.
          p
            |MyRewards is an APP with 2 millions users which offers benefits to clients of Cathay Bank. 
            |Users can exchange coupon with RedPoints and get discount through the events on the APP.
    section#objective
      sectionTitle(secTitle="Objective",secDes="Integrate TreePoint into MyReward app") 
      .paraSec
        .para
          h4 Show TreePoint of user in MyRewards
          p
            |Reveal the brand new point to MyRewards users and they will be able to see point history by date.
        .para
          h4 Replace RedPoint with TreePoint as new unit
          p
            |As the rewarding point of the whole group in the future, we have to change the unit for coupon exchange in MyRewards. 
            |However, most of users have RedPoint not TreePoint. Due to the legality, we can’t convert their points to TreePoint automatically. 
            |It’s an important issue to let users use RedPoint while they don’t even have any TreePoint.
    section#exchangeFlow
      sectionTitle(secTitle="Exchange Flow",secDes="The exsisting flow for RedPoint") 
      .paraSec
        .para
          p
            |Users will see how many RedPoints do they have on the index of MyRewards, 
            |switching to the exchange tab to see all coupons they can exchange. 
            |See details and exchange on the coupon page.
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/origin_exchange_flow.jpg")
    section#idea1
      sectionTitle(secTitle="Idea 1: Point Wallet",secDes="Put points together and convert to a total value.")
      .paraSec
        .para
          p
            |What if we create a wallet, a wallet that will contain all these points. 
            |The wallet will caculate the value of these points and add them up into a wallet value as TreePoint. 
            |Users will be able to exchange coupons with the total value of wallet. 
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/idea_pointWallet.jpg")
      .paraSec
        h3 Order to consume points
        .para
          p
            |In fact, points are not converted to TreePoints until users exchange with the value of wallet. 
            |That means there is an order to consume points. 
            |The wallet system will use TreePoint first and RedPoint second in default.
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/idea_walletRule.jpg")            
      .paraSec
        h3 Activation Flow
        .para
          p
            |Our service is provided by corporate out of financial group, 
            |so it is essential for users to activate the service themselves due to the law.
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/activationFlow.jpg")
      .paraSec
        h3 Authorization Flow
        .para
          p
            |We can’t put users’ RedPoint into wallet automatically because RedPoint is the rewarding point paid by Cathay Bank. 
            |Users have to authorize wallet service to acquire RedPoint by signing in their MyReward account.
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/authorizationFlow.jpg")
      .paraSec
        h3 Wallet Details 
        .para
          p
            |Wallet value is the sum of points value below. Points value is shown as cards, the values have been converted into TreePoint. 
            |At the bottom of the page is history which shows the changes of the wallet value. 
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/walletDetails.jpg")
      .paraSec
        h3 Exchange with wallet
        .para
          p
            |The exchange flow is nothing different but the unit is changed to TreePoint. 
            |Users won’t be worried about how many points do they need for exchange. 
            |The wallet will calculate all kinds of points into one value(TreePoint), and it is used to exchange coupons.    
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/walletFlow_exchange.jpg")
      .paraSec
        h3 Result page
        .para
          p
            |Result page shows the total value of wallet and the value after exchange. 
            |Consumption tells users how many points(total value) did it cost for the coupon exchange. 
            |In addition, it shows the detail of using, costing how many RedPoint and TreePoint.
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/walletResult.jpg")
    section#research                                               
      sectionTitle(secTitle="Validation",secDes="Test the new idea with prototypes.")
      .paraSec
        .para
          p
            |This new idea will affect 2 millions users’ experience. 
            |So we make high fidelity prototype to do usability testing to make sure users understand the wallet concept, and the experience won’t be bad.
          p
            |We acquired target users from MyRewards by push notifications with questionaire. 
            |We selected three kinds of users: users with a lot of RedPoint, users with less RedPoint and users without RedPoint.
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/walletValidation_1.jpg")
            .imgItem
              img(src="@/assets/images/treepoint/walletValidation_2.jpg")
      .paraSec
        h3 Prototypes
        .para
          p
            |We made prototype with ProtoPie. 
            |We designed three tasks for our subjects to observe how they think and do with the wallet concept. 
            |See the complete prototypes for usability testing below:
          a(href="https://cloud.protopie.io/p/8b6b735caa") SEE PROTOTYPES
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/walletIdea_1.gif")
            .imgItem
              img(src="@/assets/images/treepoint/walletIdea_2.gif")          
      .paraSec
        h3 Finding
        .para
          p
            |We wrote down what we saw on the post-it and catergorize them. 
            |Most of users can’t complete tasks successfully. Here are some conclusion we got after testing.
        .findingItem.badexp
          img(src="@/assets/images/findingIcon.png")
          span Users are confused about the concept of wallet.
        .para
          h4 Confusing unit of total value
          p
            |There will be two kinds of points in wallet, but the unit of total value is also TreePoint. 
            |That makes users confuse about the relation between TreePoint and the total value.
        .para
          h4 Bewildering relation between RedPoint and TreePoint
          p
            |RedPoint is shown as a card style on MyRewards, but there is also RedPoint shown in the wallet. 
            |It’s hard to know that these are the same.
      .paraSec
        .findingItem.badexp
          img(src="@/assets/images/findingIcon.png")
          span Users don’t accept the consumption rule of points.
        .para
          h4 They can’t determine which point is consumed first
          p
            |Users exchange coupon with value of wallet. 
            |Some users care about the order of consumption, they want to decide which point to use while exchange coupon. 
            |Because of the point system, TreePoint will be always the first one to be consumed.
      .paraSec
        .findingItem.badexp
          img(src="@/assets/images/findingIcon.png")
          span Binding process is inevitably lengthy.
        .para
          h4 Users have to sign in to bind points to the wallet
          p
            |If users want to use RedPoint and TreePoint together, they have to authorize wallet access their RedPoint by signing their account due to legality. 
            |It makes users confused and annoyed because they didn’t need to do it before.
      .paraSec
        h3 Brainstorming for revise
        .para
          p
            |We conduct a brainstorming to figure out new idea because wallet makes users confused and causes a lot of barriers for them to understand. 
            |How might users exchange coupons with RedPoint when they have not enough TreePoint. 
            |We need a new solution based on the findings.
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/walletBrain_1.jpg")
            .imgItem
              img(src="@/assets/images/treepoint/walletBrain_2.jpg")
    section#idea2
      sectionTitle(secTitle="Idea 2: No wallet & Onboarding ",secDes="New concept is not always the best choice")
      .paraSec
        .para
          p
            |What if we cracked the wallet and preserve RedPoint and TreePoint as independent cards on the MyReward index. 
            |Users don’t have to add points into wallet before using. 
            |They can still exchange coupon with RedPoint if they don’t have enough TreePoint.
      .paraSec
        h3 Onboarding and Independent card
        .para
          p
            |Information of TreePoint was revealed on onboarding pages at the first time users open MyRewards. 
            |We indicated that RedPoint was replaced with TreePoint as exchange unit and how to get TreePoints.                          
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/pointCard.jpg") 
        .para
          p
            |Like other points on MyRewards, card will tell users how many TreePoint they have. 
            |Users can see history of TreePoint by clicking the card, switching to other points with a single fling.               
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/noWalletIdea.gif")                   
      .paraSec
        h3 Confirmation during exchange flow       
        .para 
          p
            |We put a drawer of confirmation after users click “exchange” button. 
            |When users have RedPoints but inadequate TreePoint, they see a tip to let users know RedPoints will be consumed for insufficience of TreePoint. 
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/exchangeConfirm.jpg")
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/confirmDrawer_1.gif")
            .imgItem
              img(src="@/assets/images/treepoint/confirmDrawer_2.gif")              
    section#researchTwo
      sectionTitle(secTitle="Validation again",secDes="Good feedbacks without wallet")
      .paraSec
        h3 High fidelity prototype 
        .para
          p
            |This time, we make a really high fidelity prototype to do usability testing. 
            |We make onboarding pages introduce TreePoint and tips to indicate TreePoint as the new unit to exchange.
          a(href="https://cloud.protopie.io/p/441749b703") SEE PROTOTYPES
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/noWallet_prototype_1.gif")
            .imgItem
              img(src="@/assets/images/treepoint/noWallet_prototype_2.gif")              
      .paraSec
        h3 Finding
        .findingItem
          img(src="@/assets/images/findingIcon.png")
          span It’s easy to realize that there is a new point named TreePoint.
        .para
          h4 Better understanding with onboarding
          p
            |Users know TreePoint is a new point and all units has been changed to TreePoint in MyReward after going through onboarding. 
            |And they understand that TreePoint is different from RedPoint.
      .paraSec
        .findingItem
          img(src="@/assets/images/findingIcon.png")
          span It’s convenient to exchange with RedPoint when there is not enough TreePoint.
        .para
          h4 Users exchange coupon fluently
          p
            |Though we put confirmation drawer in the flow of exchange, it didn’t become a barrier for them to complete the task. 
            |They all get coupons successfully and know that it costs RedPoint when TreePoint is not enough.
      .paraSec
        .findingItem.badexp
          img(src="@/assets/images/findingIcon.png")
          span Description in the confirmation drawer is a little bit confusing.
        .para
          h4 Description of confirmation is good but confusing
          p
            |Confirmation drawer caught users attention and indicate about the consumption. 
            |However, the description is a little bit confusing. 
            |Description shows the TreePoint,  RedPoint which user owns, but users are confused about the value. 
            |It’s the value after exchange or before exchange?
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/confuseDes.jpg")             
    section#solution
      sectionTitle(secTitle="Solution",secDes="Revise confirmation and go online")
      .paraSec
        .para
          p 
            |Here are the online version after some little adjustment. We can see card of TreePoint and history of TreePoint.
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/treepoint_solution1.gif")
      .paraSec               
        .para
          p
            |Also, We revise the information of confirmation drawer and adding tips of consumption rule at the coupon list page.        
          .sectionImg
            .imgItem
              img(src="@/assets/images/treepoint/treepoint_solution2.gif")               
    section#outcome
      sectionTitle(secTitle="Outcome",secDes="Exchange with TreePoint over 40%")
      .paraSec
        .para
          p
            |After TreePoint revealed, over 70% MyRewards users owns TreePoint (Event of activation will give user 10 TreePoints) in 6 months. 
            |About 15 millions RedPoints is consumed with TreePoint until November.
          outcomeCard(v-bind:outcomeInfo="outcomeInfo")
    section#takeaway
      sectionTitle(secTitle="Takeaway",secDes="Sometimes, keeping original is a better idea")
      .paraSec
        .para
          p
            |After two user testings, I’ve learned the power of users’ habit. 
            |Don’t ignore the experience users get used to, and don’t try to  to push new feature in front of users by making big change. 
            |Sometimes, keeping the original experience is better than innovations.
    section#next
      h3 Next...
      nextProject(v-bind:nextProject="nextProject")                        
</template>

<script>
import projectNav from '@/components/projects/projectNav.vue'
import projectCard from '@/components/projects/projectCard.vue'
import sectionTitle from '@/components/projects/sectionTitle.vue'
import outcomeCard from '@/components/projects/outcomeCard.vue'
import quoteSec from '@/components/projects/quoteSec.vue'
import nextProject from '@/components/projects/nextProject.vue'
import projectRightNav from '@/components/projects/projectRightNav.vue'

export default {
  data(){
    return{
      scrollPosition: null,
      projectIntro: {
        projectName: 'TreePoint',
        projectDes: 'Brand new point system in exsisting service.',
        projectInfo: [
          {
            cate: 'Role',
            title: 'Product Designer'
          },
          {
            cate: 'Responsibility',
            title: 'UI/UX'
          },
          {
            cate: 'Organization',
            title: 'Cathay Financial Holdings'
          }
        ],
        projectTags: ['UI/UX Design', 'Mobile', 'iOS', 'Android'],
        projectDate: 'JUL. 2019 — MAY. 2020'
      },
      outcomeInfo: [
        {
          title: "Use of TreePoints",
          content: "40%+",
          description: "in MyRewards"
        },
        {
          title: "RedPoints",
          content: "15M",
          description: "consumed with TreePoint"
        },
        {
          title: "User owns TreePoint",
          content: "70%+",
          description: "of MyRewards users"
        }                
      ],      
      nextProject: {
        name: "sudo",
        date: "SEP. 2013 — OCT. 2016",
        title: "Sudo Recruit",
        summary: "We helps developers get their dream jobs.",
        tags: ["Web", "UI/UX Design", "Mobile"],
        url: "/projects/sudo"       
      },
      rightNavItem: ['background', 'idea1','research', 'idea2','solution', 'outcome']
    }
  },
  components: {
    projectNav: projectNav,
    sectionTitle: sectionTitle,
    projectCard: projectCard,
    outcomeCard: outcomeCard,
    quoteSec: quoteSec,
    nextProject: nextProject,
    projectRightNav
  },  
  methods: {
    updateScroll() {
      this.scrollPosition = window.scrollY
    }
  },
  mounted() {
    window.addEventListener('scroll', this.updateScroll);
  }   
}
</script>

<style lang="sass" scoped>
@import '@/assets/styles/color.sass' 
@import '@/assets/styles/projectStyle.sass'
#treepoint
  .heroSection
    background-image: url('~@/assets/images/treepoint/treepoint_hero.jpg')  
