<template lang="pug">
#sudo
  projectNav(projectName="Sudo Recruit")
  projectRightNav(:rightNavContent="rightNavItem")
  .heroSection(:class="{'marginTop': scrollPosition > 70}")
    projectCard(v-bind:projectCard="projectIntro")
  .viewContent
    section#background
      sectionTitle(secTitle="Background",secDes="Sudo is a start-up tries to make impacts on the market of talents")
      .paraSec
        p
          |Start-up companies emerged as starting a business became the rage in 2012- 2013. 
          |The need of developers grew, but start-up companies couldn't find fitting candidates that can fight with them.
        p
          |At that time, there are some job seeking platforms like 104 that provides plenty of jobs to job seekers. 
          |However, It didn't work for start-ups.
      .paraSec
        h3 About Sudo
        p
          |Sudo is a job matching service that focuses on start-ups and tech companies. We help developers finding jobs that they really wants. 
          |We also help start-ups finding talented developers fitting the culture well, growing with them as the company scales up.
        p
          |We aim to reduce the friction between job seeking and recruiting and accelerating the growth of start-ups with potential.

    section#problem
      sectionTitle(secTitle="Big Problem",secDes="Start with a little step as the beginning of solution")
      .paraSec
        p
          |The problem of talents market is big and complex, composed with various factors. 
          |For example, the recruiting flow on the recruiter side, the mindset of job seeking and skill of writing resume on the seeker side.
      .paraSec
        h3 Challenge: Problem is big, but resources are limited
        ul
          li Limited time
          li Insufficient fund
          li Big, complex problem
        p Sudo is a star-up too. We have to breaking the big problem into sub-problems and figure out which problem we could deal with as the pivot of our business.

    section#research
      sectionTitle(secTitle="Research",secDes="Locate the problem that we could solve in a big problem")
      .paraSec
        p We decide to dig into the journey of recruiters and job seekers. After interviews with start-up recruiters and job seekers, we have two general journey maps of recruiters and job seekers:
      .paraSec
        h3 Job Seeking Journey
        .journey_cover.fwImg
      .paraSec
        h4 Job seeking developers
        .findingItem.badexp
          img(src="@/assets/images/findingIcon.png")
          span Outdated categories and the lack of skill tags of tech jobs increases the complexity of job seeking.
        .para
          p
            |The leading platform still sorted tech jobs with outdated categories.
            br
            |The categories make it difficult to find the product they are interested in and the team they love to be a part of. Also, the lack of skill tags make it hard to find jobs matching their programming languages.
        .findingItem.badexp
          img(src="@/assets/images/findingIcon.png")
          span Basic but rough company information revealed makes job seekers hesitate and give up for applying.
        .para
          p
            |Through the interviews, we see that developers basically search for the programming language they are using as their keywords. At consideration stage, they cares about what the company do, what's the main purpose of the company and how is the atmosphere. 
            |Most of them are hesitated to apply because of the rough details about the company.
      .paraSec
        h4 For recruiters
        .findingItem.badexp
          img(src="@/assets/images/findingIcon.png")
          span Incomplete and unqualified resumes received increases the loading and difficulty of recruiting.
        .para
          p Recruiters are suffering from incomplete resume though the developers are experienced. Rough introduction and pointless description of experience are usually seen in an unqualified resume. They can't tell if the developer is the appropriate talent for the position.        
      .paraSec
        h3 Problem: Transparency
        p 
          |We can see that most of them using the leading job seeking platform in Taiwan. But there are some existing problem for them meeting each others in the market of talents. 
          |Through both the process of them, we could see that they provided inappropriate information mutually. That made it foggy when they searched for each other.          

    section#solution
      sectionTitle(secTitle="Sudo: A platform brings you into the company",secDes="A MVP that shows different information")
      .paraSec
        p
          |The platform revealed pretty basic information of the companies. It made start-ups not that attractive when compared with other big scale companies. 
          |The charm of start-ups are their cultures, their products, the atmosphere and their tenets.
        p
          |So we make a job matching platform that describes company differently. We provide the information developers really care about, helping them with decision making through job seeking.
        .sudo_flow1.fwImg
      .paraSec
        h4 Company Index Page
        p Instead of building job index listing all the opening positions, we decided to build a stage for start-ups. categorized with the service they focus on. 
        .sectionImg
          .imgItem
            img(src="@/assets/images/sudo/company_index.jpg")        
      .paraSec
        h4 Company Detail Page
        p Company page is the homepage of start-up. It shows the environment of the workspace with full-width images and you can see the key man like CEO of the company. In addition, the funding state and technical tags are shown to let developers have deeper understanding.
        .sectionImg
          .imgItem
            img(src="@/assets/images/sudo/company_detail1.jpg")
        .sectionImg
          .imgItem
            img(src="@/assets/images/sudo/company_detail2.jpg")                       
      .paraSec
        h4 Job Searching Page
        p evelopers can search vacancies with specific filter and categories suit for tech companies and start-ups. Skill tags let developers know easily if they fit this position at first. It saves their time for job seeking.
        .sectionImg
          .imgItem
            img(src="@/assets/images/sudo/job_search.jpg")           
