          p
            |Sudo's consultants will review all resumes of members before they apply for position.
            |We make sure the context can represent the talent when recruiters have a glance at it.
            br
            |Consultants will give feedbacks and suggestion to make it better.
          .sectionImg
            .imgItem
              img(src="@/assets/images/sudo/resume_edit.jpg")            
        .para
