      //.paraSec
        h3 More specific requirement and description
        p
          |After first launch, we noticed that the application rate didn't grow but the session duration was high. So we grabbed some members to our office and talked with them.        
        .para
          h4 From wide to narrow range of salary
          .findingItem
            img(src="@/assets/images/findingIcon.png")
            span Salary does matter. It affects the decision making.
          p
            |Junior developers don't know how much their value are. 
            |So they are hesitated when facing a position with wide range of salary. 
            |They are afraid that they can't meet the requirements. Though it includes requirements for mid and senior level.
          p
            |Developers in mid or senior level think differently. If the range is wide from junior to senior level, they will think the recruiters are not sure about which kind of developers they are going to recruit. 
            |Seniors will seek for more specified position.
        .para
          h4 All in one job description and requirements
          .findingItem
            img(src="@/assets/images/findingIcon.png")
            span Wide and inclusive requirements make job seekers confused.
          p
            |Recruiters used to recruit talents with a wide salary range and requirements in a single position. 
            |Because it is simple and convenient to receive applications. 
          p 
            |However, it makes job seekers halt becasue they think they won't meet all the requirements. 
            |It becomes a strict standard for them.
      .paraSec
        h3 Handy tool facilitates recruiting process
        p
          |We concern about the stickiness of recruiters. But, recruiters don't use our platform for managing their talent pool as we think. 
          |The application status won't be transparent if recruiters don't update the status of application from the backstage.
        .para
          .findingItem
            img(src="@/assets/images/findingIcon.png")
            span Recruitment processes are various. It's different between companies.
          p
            |Recruiters said, We have plenty of channel for recruitment like Sudo and 104. So checking the applications through platforms is time-costing job. 
            |We always collect resumes from platforms and import to our recruiting tool.
          p
            |Platform should be a handy tool for recruiters. We can't force them stick on our service if Sudo is considered as a channel among these job matching service.
        .para
          h4 Functional email with resume
          p Recruiters will receive the new email from Sudo when job seekers apply for positions. Also, they can manage the application easily by clicking button in email.
          .sectionImg
            .imgItem
              img(src="@/assets/images/sudo/resumeEmail_1.jpg")
            .imgItem
              img(src="@/assets/images/sudo/resumeEmail_2.jpg")                

    section#library
      sectionTitle(secTitle="Scale Up Design",secDes="As the service grows, design should be DRY as engineering")
      .paraSec
        p
          |I'm the only one product designer in the team. Facing changes and incoming needs, I have to create UI efficiently. 
          |So I collaborate with frontend developer and build a basic design library.
        .library_cover.fwImg
    section#takeaways
      sectionTitle(secTitle="Takeaways",secDes="A great experience facing two kinds of users from the company side and employee side")
      .paraSec
          p
            |It is my first design job and I am anxious as the only product designer in the start-up. 
            |Also, it is the first job for other members. Everything is brand new for us to learn. We learn agile method for product development, and we learn to make pitching slides attractive. 
            |I learn basic frontend development because my first UI was not rational. Dealing with Github and making pull request is a normal routine after I join the development.
          p
            |Though Sudo ended in 2016, I still have connection with our team. We make side projects together and discuss with each other about our career path.
          .sectionImg
            .imgItem
              img(src="@/assets/images/sudo/sudo_team.jpg")            
      .paraSec
        h4 It's not a seesaw with balance but a wheel
        p
          |I always think platform service as making balance with stakeholders. But after all of this, I think it is a wheel. We revealed the information of start-up and polish them with what they're good at. 
          |We provided the information and consult service that developers need for resume writing or interviews.
        p 
          |It's like fueling to these two sides of wheel and makes the wheel go faster and faster. Recruiters find qualified talents and developers find their best stage.
      .paraSec
        h4 Learn from MVP
        p
          |We try to figure out a solution from a big, vague problem. MVP is an assumption we made after we interviewed a lot of users (job seekers, recruiters). Soon, we see something weird from the metrics. So we interview with our clients and Sudo members. 
          |It helps us to dig deeper into the experience of recruiting and job seeking. 
        p 
          |We make some adjustment, the wheel start going fast. I know it was not the best solution, and I will keep iterating the product to make sure the wheel keep going.
      .paraSec
        h4 I become a self-taught designer
        p
          |The time and funding is limited, so I would like to learn anything that help our team getting better, being more efficient. 
          |I become self-taught after this precious experience.
