<template lang="pug">
#projectNav(:class="{'fixed': scrollPosition > 70}")
  .navContent
    router-link(to="/").back
      img(src="~@/assets/images/chevron_left.png")
    .projectName {{ projectName }}
</template>

<script>
export default {
  data(){
    return {
      scrollPosition: null
    }
  },
  props: [
    "projectName"
  ],  
  methods: {
    updateScroll() {
      this.scrollPosition = window.scrollY
    }    
  },
  mounted() {
    window.addEventListener('scroll', this.updateScroll);
  }   
}
</script>

