import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/pages/Home.vue'
import NineYi from '@/pages/projects/nineyi.vue'
import TreePoint from '@/pages/projects/treepoint.vue'
import PartyAnimals from '@/pages/prototypes/partyAnimals_proto.vue'
import CafeFeeds from '@/pages/prototypes/cafeFeeds_proto.vue'
import InteractionLab from '@/pages/prototypes/interactionLab_proto.vue'
import TreePointProto from '@/pages/prototypes/treepoint_proto.vue'
import Sudo from '@/pages/projects/sudo.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '@/pages/About.vue')
  },
  {
    path: '/projects/sudo',
    name: 'Sudo Recruit',
    component: Sudo
  },
  {
    path: '/projects/nineyi',
    name: '91APP',
    component: NineYi
  },
  {
    path: '/projects/treepoint',
    name: 'TreePoint',
    component: TreePoint
  },
  {
    path: '/prototypes/partyanimals',
    name: 'Party Animals',
    component: PartyAnimals
  },
  {
    path: '/prototypes/interactionlab',
    name: 'Interaction Lab',
    component: InteractionLab
  },
  {
    path: '/prototypes/cafefeeds',
    name: 'Cafe Feeds',
    component: CafeFeeds
  },
  {
    path: '/prototypes/treepointproto',
    name: 'TreePoint Proto',
    component: TreePointProto
  }  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition      
    } else {
      return { x: 0, y: 0 }
    }
  }      
})

export default router
